export default {
	data() {
		return {
			refreshing: false,
			loadMoreText: "加载中...",
			lists: [],
			id: 0,
			fetchPageNum: 0
		}
	},
	onLoad(e) {
		this.id = e.id;
	},
	onPullDownRefresh() {
		console.log("下拉刷新");
		this.refreshing = true;
		this.getData();
	},
	onReachBottom() {
		console.log("上拉加载刷新");
		if (this.fetchPageNum > 4) {
			this.loadMoreText = "没有更多了"
			return;
		}
		this.getData();
	},
	methods: {

	}
}
